
import Search from "@/components/search";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { JDGetIncomingList } from "@/api/getData";
export default {
data() {
return {
result: [],
showToast: false,
value1: "2015-11-12",
FInterID: ""
};
},
directives: {
TransferDom
},
components: {
Tabbar,
TabbarItem,
XButton,
XDialog
},
mounted() {
this.getIncomingList();
},
methods: {
async getIncomingList() {
let data = await JDGetIncomingList({
StartDate: "",
EndDate: "",
Supplier: "",
Type: "1"
});
console.log(eval(data));

this.result = eval(data);
},
toInfo(FInterID) {
this.showToast = true;
this.FInterID = FInterID;




},
doShowToast() {
console.info(this.FInterID)
this.$router.push({
path: "/auditForm/inspectionAdd",
query: { FInterID: this.FInterID }
});
this.showToast = false;
},
doShowToast1() {
console.info(this.FInterID)
this.$router.push({
path: "/auditForm/inspectionDeal",
query: { FInterID: this.FInterID }
});
this.showToast = false;
}
}
};



